.form {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  margin: 50px;
  width: 220px;
}

.label {
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  margin-bottom: 10px;
}

.loginButton {
  width: max-content;
  align-self: flex-end;
}

.authError {
  height: 25px;
  color: red;
}